<template>
  <div id="app">
    <transition name="fade">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>
  </div>
</template>

<script type="javascript">
  export default {
    name: 'app',
    data() {
      return {
        userInfo: {
          nick: null,
          uid: null
        }
      }
    },
    created() {
      //
      this.checkLogin()
    },
    mounted() {
      this.getUserInfo()
    },
    watch: {
      // 
      // cookie/storage
      // ()
      // 
      '$route': 'checkLogin'
    },
    methods: {
      checkLogin() {
        if(!this.getCookie('session')) {
          this.$router.push('/login')
        }
      },
      getUserInfo() {
        // request userInfo
        this.userInfo = {
          nick: 'Leecason',
          uid: 10000
        }

        this.$store.commit('updateUserInfo', this.userInfo);
      }
    }
  }
</script>

<style>
  #app {
    height: 100%;
  }
</style>



